import React from 'react';
import {getRandomColor} from './helper';
import logo from '../../assets/images/logo.svg';

class Product extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            loading: true,
            product: [],
        }
        
    }

    // Fetch data through API

    componentDidMount() {
        const { id } = this.props.match.params;

        fetch(`https://jsonplaceholder.typicode.com/posts/${id}`)
            .then(res => res.json()).then(json =>
                this.setState({
                    loading: false,
                    product: json,
                })
            );
    }

    // Change Image
    changeImg(e){
        var tmpsrc = e.target.getAttribute('src');
        let curSrc = tmpsrc.substring(tmpsrc.length - 6);
        let bigImg = document.getElementById("det-img-big").src;
        bigImg = bigImg.substring(0, bigImg.length - 6);
        document.getElementById("det-img-big").src = bigImg + curSrc;
    }

    render() {
        var { loading, product, dat } = this.state;
        if (loading) {
            return (
                <div className="loading"> <p>Loading......</p> <img src={logo}/> </div>
            );
        } else {
            return (
                <div className="product">
                    <div className="fade-in left">
                        <div className="innerLeft">
                            <span className="fade-in img-holder">
                                <img onClick={this.changeImg} src={getRandomColor(11)} />                            
                            </span>
                            <span className="fade-in img-holder">
                                <img onClick={this.changeImg} src={getRandomColor(11)} />                            
                            </span>
                            <span className="fade-in img-holder">
                                <img onClick={this.changeImg} src={getRandomColor(11)} />                            
                            </span>
                            <span className="fade-in img-holder">
                                <img onClick={this.changeImg} src={getRandomColor(11)} />                            
                            </span>
                        </div>
                        <div className="innerRight">
                            <img className="fade-in" id="det-img-big" src={getRandomColor(2.3)}/>
                        </div>
                        <div className="cboth"></div>    
                    </div>
                    <div className="fade-in right">
                        <h4 className="pd-title">{product.title}</h4>
                        <p className="des">
                            {product.body}                            
                        </p>
                    </div> 
                    <div className="cboth"></div>
                </div>
            );
        }
    }

}


export default Product;