import React from 'react';
import { Link } from 'react-router-dom';
import {getRandomColor,stringTrim} from './helper';
import logo from '../../assets/images/logo.svg';

class Shop extends React.Component {
  productSize = 0;

  constructor(props) {
    super(props)
    this.state = {
      loading: true,
      produtcs: [],
      hasErrored: false
    }

    // Resize image as per window size

    if (window.innerWidth > 1050){
      this.productSize =4;
    }else if (window.innerWidth > 700){
      this.productSize = 2;
    }else{
      this.productSize = 1;
    }

  }

  // Fetch data through API

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/posts')
      .then(res => res.json()).then(json =>
        this.setState ({
          loading: false,
          produtcs: json
        })
      )
  }

  render() {
    var { loading, produtcs } = this.state;
    if (loading) {
      return (
        <div className="loading"> <p>Loading......</p> <img src={logo}/> </div>
      );
    } else {
      return (
        <div className="shop">
          <ul>
            {produtcs.map(product => (
              <li className="fade-in" key={product.id}>
                <span className="img-holder">
                  <img src={getRandomColor(this.productSize)} />
                </span>
                <Link to={'/product/'+ product.id}>
                  <h5 className="pd-title">{stringTrim(product.title, 30)}<span className="txt-shad"></span></h5>
                </Link>
                <p className="des">
                  {stringTrim(product.body, 86)}
                  <span className="txt-shad"></span>
                </p>
              </li>
            ))}
            <div className="cboth"></div>
          </ul>
        </div>
      );
    }

  }

}

export default Shop;