// All Common Helper Functions

export const getRandomColor = (sz)=> {
    var letters = 'ABCDEF0123456789';
    var color = '';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    var size = window.innerWidth / sz;
    return 'https://via.placeholder.com/' + Math.round(size) + '/' + color;
  }

export const stringTrim = (str, len)=> {
        var trimmedString = str.substring(0, len);
        return trimmedString;
      }