import React from 'react';
import './slider.css';
import slideImg1 from '../../assets/images/freestocks-org-187367-unsplash.jpg';
import slideImg2 from '../../assets/images/fezbot2000-65194-unsplash.jpg';
import slideImg3 from '../../assets/images/clem-onojeghuo-150467-unsplash.jpg';
import slideImg4 from '../../assets/images/senjuti-kundu-349558-unsplash.jpg';
import slideImg5 from '../../assets/images/chuttersnap-165289-unsplash.jpg';

export default class Slider extends React.Component {    
    intervalID = 0;
    delay= 3000;
    
    constructor(props) {
        super (props)
        
        this.state = {
                images : [
                    slideImg1,
                    slideImg2,
                    slideImg3,
                    slideImg4,
                    slideImg5
                ],
            currentIndex: 0,
            translateValue: 0,
            trans: 'transform ease-out 0.45s'            
        }

        
    }

    componentDidMount(){
           this.intervalID = setInterval(this.nextSlide, this.delay); 
    }

    componentWillUnmount(){               
            clearInterval(this.intervalID);
            clearInterval(this.timer);
    }

    prvsSlide = ()=> {
        if(this.state.currentIndex <= 0 ){
            //console.log('In....');
            this.setState({
                currentIndex: 0,
                translateValue: 0,
                trans: 'transform ease-out 0.45s'
            });
        }else{
            this.setState(prevState => ({
                currentIndex: prevState.currentIndex - 1,
                translateValue: prevState.translateValue + window.innerWidth,
                trans: 'transform ease-out 0.45s'
                })
            );
        }
    }

    nextSlide = ()=> {
        if(this.state.currentIndex === (this.state.images.length - 1) ){
           // console.log('In....');
            this.setState({
                currentIndex: 0,
                translateValue: 0,
                trans:`none`
            });
        }else{
            this.setState(prevState => ({
                currentIndex: prevState.currentIndex + 1,
                translateValue: prevState.translateValue - window.innerWidth,
                trans: 'transform ease-out 0.45s'
                })
            );
            console.log('Running......');
        }
    }
    

    render (){
        return(
            <div className="slider">
                <div className="slider-wrapper"
                    style={{
                        width: `${window.innerWidth * 8}px`,
                        transform: `translateX(${this.state.translateValue}px)`,
                        transition: `${this.state.trans}`
                    }}>
                    {
                        this.state.images.map( (img,i)=>(
                            <Slide key={i} imgUrl={img} />
                        ))
                    }
                </div>
                <div className="left" onClick={this.prvsSlide} onTouchStart={this.prvsSlide}>
                    &lt;
                </div>
                <div className="right" onClick={this.nextSlide} onTouchStart={this.nextSlide}>
                    &gt;
                </div>                
            </div>
        );
    }
}

const Slide = ({imgUrl}) =>{
    return(
        <div className="slide" style={{width:`${window.innerWidth}px`}}>
            <img src={imgUrl}></img>   
        </div>
    );
}

