import React from  "react";
import {Link} from 'react-router-dom';

export default (props) => {
    return(
        <div className="header">
            <a className="logo">LOgO</a>
            <nav>
                <ul>
                    <li>
                        <Link to="/home">Home</Link>
                    </li>
                    <li>
                    <Link to="/shop">Shop</Link>
                    </li>
                </ul>                
            </nav>
            <span className="cboth"></span>
        </div>
    );
};

